html {
  margin: 0;
}
body {
  margin: 0;
}
.top {
  position: fixed;
  height: 38px;
  width: 100%;
  background-color: #232323;
  font-size: 10px;
  font-family: helvetica arial sans-serif;
  margin: 0;
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  color: white;
  z-index: 1;

}

.white {
  margin: 0;
  top: 38px;
  border-bottom: 1px black;
  height: 58px;
  width: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  background-color: white;
  padding: auto;
  z-index: 1
}

.itemscon {
  position: relative;
  display: flex;
  justify-content: flex-end;
  height: 30px;
  width: 600px;
  float: right;
  border: 1px white solid;
 }

.topitems {
  height: 50px;
  width: 125px;
  margin: 0 10px;
  position: relative;
  display: inline-block;
}

.subaruhome {
  margin: 11px 0 0 23px;
  position: relative;
	background: url('../assets/imgs/siteimgs.PNG') no-repeat -1px 0;
	width: 150px;
	height: 40px;
  border: none;
}
.centermenu {
  position: relative;
  display: inline-block;
  height: 25px;
  width: 500px;
  margin: 0 auto;
}
.items {
  margin: 0;
  height: 25px;
  width: 150px;
  font-size: 12px;
  display: inline-block;
  text-align: center;
}

.sideitems {
  height: 30px;
  width: 450px;
  position: relative;
  display: flex;
  border: 1px solid black;
}

.side {
  height: 30px;
  width: 200px;
  display: flex;
  flex-direction: row;
}
.side h4 {
  font-size: 12px;
}

.byo {
  background: url("../assets/imgs/byo.gif");
  height: 30px;
  width: 30px;
}

.byo:hover {
  background: url("../assets/imgs/byo.gif") -30px;
  height: 30px;
  width: 30px;
}

.fad {
  background: url("../assets/imgs/fad.gif");
  height: 30px;
  width: 30px;
}

.fad:hover {
  background: url("../assets/imgs/fad.gif") -30px;
  height: 30px;
  width: 30px;
}

.footersociallinks {
  position: relative;
}


