.small-only {
  display: none;
}

.ui-link {
  position: relative;
  display: inline-flex;
}

.zip_modal_input {
  display: none;
}

p {
  height: auto;
  width: auto;
  display: none;
}

.map_search_input {
  display: none;
}

.change_retailer_modal {
  display: none;
}

.zip_code.far_zipCode {
  display: none;
}

.footer {
  height: 603px;
  width: 100%;
  background-color: #232323;
  position: absolute;
  display: inline-flex;
  justify-content: center;
  flex-wrap: wrap;
  color: white;
}
